* {
  font-family: "Raleway", sans-serif;
}

.red-text {
  color: #A91E24;
}

mark {
  background: none;
}

h1 mark.red-text {
  display: inline;
}

h2 mark.red-text, h3 mark.red-text, h4 mark.red-text, h5 mark.red-text, h6 mark.red-text, p mark.red-text, li mark.red-text {
  display: inline;
}

li p {
  margin: 0;
}

.aboveTheFold {
  position: relative;
  display: flex;
  flex-direction: column;
}
.aboveTheFold::before {
  content: " ";
  opacity: 0.2;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-image: url(/images/GAPIcon.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: left center;
  z-index: -1;
}
@media screen and (max-width: 450px) {
  .aboveTheFold::before {
    background-size: auto 500px;
    background-position: bottom left;
  }
}
.aboveTheFold__title {
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 1em;
  justify-content: center;
}
.aboveTheFold__title h1 {
  font-size: 8em;
  margin: 0;
  margin: 0 0.15em;
  white-space: initial !important;
}
@media screen and (max-width: 450px) {
  .aboveTheFold__title h1 {
    font-size: 6em;
  }
}
.aboveTheFold__title img {
  min-width: 280px;
  max-width: 500px;
}
.aboveTheFold__heroImages {
  max-width: 1280px;
  margin: 0 auto;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
}
.aboveTheFold__heroImages * {
  z-index: 1;
}
.aboveTheFold__heroImages__heroImage figure {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.aboveTheFold__heroImages__heroImage figure img {
  width: 500px;
  clip-path: circle(165px);
}
@media screen and (max-width: 598px) {
  .aboveTheFold__heroImages__heroImage figure img {
    width: 400px;
    clip-path: circle(134px);
  }
}
@media screen and (max-width: 497px) {
  .aboveTheFold__heroImages__heroImage figure img {
    width: 300px;
    clip-path: circle(100px);
  }
}
.aboveTheFold__heroImages__heroImage figure figcaption.red-text {
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;
}
.aboveTheFold__heroImages__heroImage__link {
  margin-top: 0.5em;
  text-decoration: none;
  color: white;
  transition: background-color 0.3s ease-out;
  background-color: #080180;
  border-radius: 3px;
  padding: 0.5em 1em;
}
.aboveTheFold__heroImages__heroImage__link:hover {
  background-color: #05014d;
}
.aboveTheFold__heroImages__heroImage__link:visited {
  color: white;
}

.founderCopy {
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;
  padding: 2em;
}
.founderCopy p {
  max-width: 1280px;
  margin: 1em auto;
}

.subtitle {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3.5em;
  text-align: center;
  padding: 1em;
}
.subtitle mark {
  white-space: nowrap;
  color: #A91E24;
}
@media screen and (max-width: 450px) {
  .subtitle {
    font-size: 2em;
  }
}

.team {
  display: flex;
  flex-wrap: wrap;
  padding: 2em;
  justify-content: center;
  border-bottom: 1em solid #A91E24;
}
.team__card {
  min-width: 300px;
  max-width: 325px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: black;
  margin: 1em;
  padding: 1em;
  text-decoration: none;
}
.team__card figure {
  flex: 1;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.team__card figure img {
  height: 284px;
  clip-path: circle(117px);
  max-width: 100vw;
}
.team__card figure figcaption.red-text {
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;
}
.team__card__link {
  text-decoration: none;
  color: white;
  transition: background-color 0.3s ease-out;
  background-color: #080180;
  border-radius: 3px;
  padding: 0.5em 1em;
}
.team__card__link:hover {
  background-color: #05014d;
}
.team__card__link:visited {
  color: white;
}

.raulRizoPatronAdjustment {
  width: 395px;
}